import React, { Fragment } from "react";
import styled from "styled-components";
import PropTypes from "prop-types";

const ItemWrapper = styled.div`
  display: inline-block;
  position: relative;
  max-width: 360px;
  margin: 24px 20px;
  font-size: 14px;
  vertical-align: top;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.1);
`;

const ItemNumber = styled.div`
  position: absolute;
  top: 7px;
  right: 5px;
  width: 22px;
  height: 22px;
  padding: 22px;
  font-size: 22px;
  text-align: center;
  border-radius: 50px;
  color: #fff;
  background: ${props => (props.bools ? "#f00" : "#5af")};
  box-sizing: content-box;
`;

const ItemTitle = styled.div`
  min-height: 96px;
  padding: 8px;
  border: solid 1px #e6e6e6;
  border-top: solid 3px ${props => (props.bools ? "#f00" : "#5af")};
`;

const ItemContent = styled.div`
  min-height: 240px;
  padding: 8px;
  border: solid 1px #e6e6e6;
  border-top: none;
`;

const Item = ({ id, title, content, bools, onToggle }) => {
  const contentFormat = content.split("\n").map((line, idx) => {
    return (
      <Fragment key={idx}>
        {line}
        <br />
      </Fragment>
    );
  });
  return (
    <ItemWrapper onClick={onToggle}>
      <ItemNumber bools={bools}>{id}</ItemNumber>
      <ItemTitle bools={bools}>{title}</ItemTitle>
      <ItemContent>{contentFormat}</ItemContent>
    </ItemWrapper>
  );
};

Item.propTypes = {
  id: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  content: PropTypes.string.isRequired,
  bools: PropTypes.bool,
  onToggle: PropTypes.func.isRequired
};
Item.defaultProps = {
  bools: false
};
ItemTitle.propTypes = {
  bools: PropTypes.bool
};
ItemTitle.defaultProps = {
  bools: false
};

export default Item;



